<template>
  <div class="user">
    <el-card>
      <!--  -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ name: 'index' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{name:'user'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <topSearch :data="query.keyword" @t-enter="handleEnter(fetchUser,$event)"
        @t-close="handleClose(fetchUser)" text="" @t-btn="showUserDialog(0)" />
      <!--  -->
      <el-table :data="userList" stripe style="width: 100%" max-height="600">
        <el-table-column prop="avatarImgUrl" label="">
          <template v-slot="{row}">
            <el-avatar shape="square" :size="40" :src="bindURL(row.avatarImgUrl)">
            </el-avatar>
          </template>
        </el-table-column>
        <el-table-column prop="username" label="" min-width="120">
        </el-table-column>
        <el-table-column prop="name" label="" min-width="100">
        </el-table-column>
        <el-table-column prop="qq" label="QQ" min-width="120"> </el-table-column>
        <el-table-column prop="phone" label="" min-width="120">
        </el-table-column>
        <el-table-column prop="email" label="" min-width="160">
        </el-table-column>
        <el-table-column prop="level" label="" min-width="80">
          <template v-slot="{row}">
            <el-tag :type="levels[row.level].type" v-if="levels">
              {{levels[row.level].value}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="150">
          <template v-slot="{row}">
            <el-link :underline="false" type="primary" @click="showUserDialog(1,row)">
            </el-link>
            <el-link :underline="false" type="danger"
              @click="deleteById(_deleteUser,fetchUser,row.id,'')"></el-link>
            <el-link :underline="false" type="info" @click="resetPassword(row.id)">
            </el-link>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange(fetchUser,$event)"
        @current-change="handleCurrentChange(fetchUser,$event)" :current-page="query.page"
        :page-sizes="[1, 2, 5, 10]" :page-size="query.size"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>

    </el-card>
    <!--  -->
    <el-dialog :title="userForm.flag===0?'':''" :visible.sync="userDialogVisible"
      width="30%" @close="clearDialog('userForm')">
      <el-form :model="userForm" :rules="userRules" ref="userForm" label-width="100px"
        size="small" class="user-dialog-form">
        <el-form-item prop="avatarImgUrl" label="" v-if="userForm.flag === 1">
          <el-upload class="avatar-uploader" :action="bindURL('/uploadfile')" name="files"
            :show-file-list="false" :on-success="handleAvatarSuccess">
            <img v-if="userForm.avatarImgUrl" :src="bindURL(userForm.avatarImgUrl)"
              class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="userForm.username" :disabled="userForm.flag === 1">
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="userForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" v-if="userForm.flag === 0">
          <el-input v-model="userForm.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="QQ" prop="qq">
          <el-input v-model="userForm.qq"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="userForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="userForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="level">
          <el-radio v-for="level in levels" :label="level.id" v-model="userForm.level">
            {{level.value}}
          </el-radio>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input type="textarea" autosize placeholder=""
            v-model="userForm.description" :autosize="{minRows:2,maxRows:4}"
            resize="none">
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="userDialogVisible = false" size="small"> </el-button>
        <el-button type="primary" @click="submitUser(userForm.flag,'userForm')"
          size="small">{{userForm.flag === 0? '': ''}}
        </el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
import { aMixin } from '@mixins'
import {
  _getUserList,
  _deleteUser,
  _changePassword,
  _addUser,
  _editUser
} from '@api'
import {
  checkEmail,
  checkPhone,
  bindURL,
  convertDeepCopy,
  convertURL,
  getUid
} from '@utils'
import { ADD, EDIT, DEFAULT_PWD, levels } from '@static'
export default {
  data() {
    return {
      userList: [],
      levels,
      userForm: {},
      userRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        qq: [{ required: true, message: 'qq', trigger: 'blur' }],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        level: [{ required: true, message: '', trigger: 'blur' }],
        avatarImgUrl: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      userDialogVisible: false
    }
  },
  methods: {
    bindURL,
    _deleteUser,
    // 
    async fetchUser() {
      const { list, total } = await _getUserList(this.query)
      this.userList = list
      this.total = total
    },
    // 
    showUserDialog(flag, data) {
      this.userDialogVisible = true
      switch (flag) {
        case ADD:
          this.userForm.flag = ADD
          break
        case EDIT:
          this.userForm = convertDeepCopy(data)
          this.userForm.flag = EDIT
          break
      }
    },
    submitUser(flag, formName) {
      this.$refs[formName].validate(async (valid) => {
        if (!valid) return
        this[formName].updateTime = Date.now()
        if (flag === ADD) {
          this[formName].id = getUid()
          this[formName].state = 0
          const { success } = await _addUser(this[formName])
          this.handleSuccess(success, '')
        } else if (flag === EDIT) {
          const { success } = await _editUser(this[formName])
          this.handleSuccess(success, '')
        }
        this.userDialogVisible = false
        this.fetchUser()
      })
    },
    // 
    handleAvatarSuccess(res, file) {
      this.$set(this.userForm, 'avatarImgUrl', res)
    },
    // 
    async resetPassword(id) {
      const success = await _changePassword({ id, password: DEFAULT_PWD })
      if (success) {
        this.$message.success(':123456')
      } else {
        this.$message.error('')
      }
    }
  },
  mixins: [aMixin],
  created() {
    this.fetchUser()
  }
}
</script>

<style lang="less" scoped>
@import '~@css/variables.less';
@import '~@css/common.less';
</style>
