<template>
  <div class="change-password">
    <el-form :model="passwordForm" :rules="passwordRules" ref="passwordForm"
      label-width="100px" size="small">
      <el-form-item label="" prop="oldPassword">
        <el-input v-model="passwordForm.oldPassword" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newPassword">
        <el-input v-model="passwordForm.newPassword" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="confirmPassword">
        <el-input v-model="passwordForm.confirmPassword" type="password"></el-input>
      </el-form-item>
      <el-form-item class="btn">
        <el-button type="info" @click="resetForm('passwordForm')"></el-button>
        <el-button type="success" @click="submitForm('passwordForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { _changePassword } from '@api'
export default {
  name: 'change-password',
  props: {
    id: {
      type: Number
    }
  },
  data() {
    const checkPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.passwordForm.newPassword) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      passwordForm: {},
      passwordRules: {
        oldPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPassword, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) return
        const success = _changePassword({
          id: this.id,
          password: this.passwordForm.confirmPassword
        })
        if (success) {
          this.$message.success('')
          this.resetForm('passwordForm')
        } else {
          this.$message.error('')
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.el-form-item.btn.el-form-item--small {
  text-align: right;
  margin-right: 20px;
}
</style>
