<template>
  <div class="discuss">
    <el-card>
      <!--  -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: 'index' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{path:'discuss'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <top-search :data="query.keyword" @t-enter="handleEnter(fetchPost,$event)"
        @t-close="handleClose(fetchPost)" text="" :showAdd="false" />
      <el-table stripe style="width: 100%" :data="PostList">
        <el-table-column prop="title" label="" min-width="180">
        </el-table-column>
        <el-table-column prop="authorId" label="">
          <template v-slot="{row}">
            {{row.author.name}}
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="" min-width="120">
          <template v-slot="{ row }">
            {{ row.createTime | formatDate }}
          </template>
        </el-table-column>
        <el-table-column prop="updateTime" label="" min-width="120">
          <template v-slot="{ row }">
            {{ row.updateTime | formatDate }}
          </template>
        </el-table-column>
        <el-table-column prop="lastReplyTime" label="" min-width="120">
          <template v-slot="{ row }">
            {{ row.lastReplyTime | formatDate }}
          </template>
        </el-table-column>
        <el-table-column prop="replyNum" label="" min-width="80">
          <template v-slot:default="scope">
            <el-tag effect="plain" type="info"
              :class="scope.row.replyNum > 0 ? 'myNum' : ''">
              {{ scope.row.replyNum }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80">
          <template v-slot="{row}">
            <el-link :underline="false" type="primary" @click="goPostDetail(row)">
            </el-link>
            <el-link :underline="false" type="danger"
              @click="deleteById(_deletePost,fetchPost,row.id,'')"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange(fetchPost,$event)"
        @current-change="handleCurrentChange(fetchPost,$event)" :current-page="query.page"
        :page-sizes="[1, 2, 5, 10]" :page-size="query.size"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { _getPostList, _deletePost } from '@api'
import { aMixin } from '@mixins'
import { mapGetters, mapMutations } from 'vuex'
export default {
  data() {
    return {
      PostList: [],
      selectIndex: 1
    }
  },
  methods: {
    _deletePost,
    ...mapMutations(['setCurrentPost']),
    // 
    async fetchPost() {
      const { list, total } = await _getPostList(this.query)
      this.PostList = list
      this.PostList.forEach((item) => {
        item.author = this.getUserById(item.authorId)
      })
      this.total = total
    },
    // 
    goPostDetail(data) {
      this.setCurrentPost(data)
      this.$router.push(`/discuss/${data.id}`)
    }
  },
  computed: {
    ...mapGetters(['getUserById'])
  },
  mixins: [aMixin],
  created() {
    this.fetchPost()
  }
}
</script>

<style lang="less" scoped>
@import '~@css/common.less';
</style>
