import topSearch from '@components/common/TopSearch'
export const aMixin = {
    data () {
        return {
            query: {
                page: 1,
                size: 10,
                keyword: null
            },
            total: 0
        }
    },
    methods: {
        // 
        handleSizeChange (callback, value) {
            this.query.size = value
            this.query.page = 1
            callback()
        },
        // 
        handleCurrentChange (callback, value) {
            this.query.page = value
            console.log(this.query.page)
            callback()
        },
        // 
        handleClose (callback) {
            this.query.keyword = null
            callback()
        },
        // enter
        handleEnter (callback, value) {
            this.query.keyword = value
            callback()
        },
        // id
        deleteById (delCallback, fetchCallback, id, info) {
            this.$confirm('' + info + ', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'error',
                center: true
            })
                .then(async () => {
                    const { success } = await delCallback(id)
                    if (success) {
                        this.$message.success('')
                        fetchCallback()
                    } else {
                        this.$message.error('')
                    }
                })
                .catch(() => {
                    this.$message.warning('')
                })
        },
        // 
        handleSuccess (success, info, callback) {
            if (success) {
                this.$message.success(`${info}`)
                callback && callback()
            } else {
                this.$message.error(`${info}`)
            }
        },
        // 
        clearDialog (formName) {
            this.$refs[formName].resetFields()
            this[formName] = {}
        }
    },
    components: {
        topSearch
    }
}

